import React from "react"
import PropTypes from "prop-types"
import {
  TableRow,
  TableCell,
  Table,
  TableHead,
  TableBody,
  Typography,
  IconButton,
} from "@material-ui/core"
import produce from "immer"

class Cache {
  super = new Map()

  get(key) {
    return this.super.get(key)
  }

  set(k, v) {
    this.super.set(k, v)
    return v
  }

  has(k) {
    return this.super.has(k)
  }
}
const { shape } = PropTypes

class Interface {
  static get [Symbol.species]() {
    return this.prototype.constructor
  }

  static construct() {
    const This = this[Symbol.species]

    return new This()
  }

  static isInterface(o) {
    return o instanceof Interface
  }
}

PropTypes.shape = (...args) => {
  const Shape = shape.bindApply(PropTypes)
  const [klass] = args
  if (Interface.isInterface(klass)) {
    return Shape([klass.construct(), ...args])
  }
  return Shape(args)
}

function memo(f) {
  const cache = new Cache()
  return (...args) => {
    const key = args[0]
    if (cache.has(key)) {
      return cache.get(key)
    }
    const value = f(...args)

    return cache.set(key, value)
  }
}
const { timeZone } = Intl.DateTimeFormat().resolvedOptions()

class MessageThread extends React.PureComponent {
  render() {
    const { date, time, message, msgType } = this.props
    return (
      <TableRow>
        <TableCell>
          <Typography variant="p" align="center">
            {date}
          </Typography>
        </TableCell>
        <TableCell>
          <Typography variant="p" align="center">
            {message}
          </Typography>
        </TableCell>
        <TableCell>
          <Typography variant="p" align="center">
            {msgType}
          </Typography>
        </TableCell>
        <TableCell>
          <Typography variant="p" align="center">
            {time}
          </Typography>
        </TableCell>
      </TableRow>
    )
  }
}

const CustomCell = ({ children, sibiling }) => (
  <TableCell>
    <Typography variant="h5" align="center">
      {children}
    </Typography>
    {sibiling}
  </TableCell>
)
const toInt = n => Number.parseInt(n, 10)
const toSeconds = t => {
  const [key, period] = t.split(" ")
  const [hrs, min] = key.split(":").map(toInt)
  if (period === "PM") {
    const militaryHours = hrs + 12

    return (militaryHours > 23 ? 0 : militaryHours) * 3600 + min * 60
  }

  return hrs * 3600 + min * 60
}

const checkOrder = (order, key) => (order[key] === "ASC" ? "↓" : "↑")
const Headers = ({ sort, order }) => (
  <TableHead>
    <TableRow>
      <CustomCell
        sibiling={
          <IconButton onClick={sort("date")}>
            {checkOrder(order, "date")}
          </IconButton>
        }
      >
        Date
      </CustomCell>
      <CustomCell>Message</CustomCell>
      <CustomCell>Type</CustomCell>
      <CustomCell
        sibiling={
          <IconButton onClick={sort("time")}>
            {checkOrder(order, "time")}
          </IconButton>
        }
      >
        {`Time 
      (${timeZone})`}
      </CustomCell>
    </TableRow>
  </TableHead>
)
function Enum(keys, vals = []) {
  return keys.reduce(
    (main, key, i) => ({
      ...main,
      [key]: vals[i] || i,
      [vals[i] || i]: key,
    }),
    {}
  )
}
const OrderEnum = Enum(["ASC", "DESC"], [-2, 1])
const sorter = (order, cb) => {
  const sign = Math.sign(OrderEnum[order])
  return (...args) => {
    const res = sign * cb(...args)
    console.log({ res, sign, args })
    return res
  }
}
export default class Message extends React.PureComponent {
  state = {
    sort: {
      date: OrderEnum.DESC,
      time: OrderEnum.DESC,
    },
  }

  onClick = memo(type => {
    let sort = {
      asc: null,
      desc: null,
    }

    const boundSort =
      type === "date"
        ? method =>
            sorter(
              method,
              ({ date: d1 }, { date: d2 }) => new Date(d1) - new Date(d2)
            )
        : method =>
            sorter(
              method,
              ({ time: t1 }, { time: t2 }) => toSeconds(t1) - toSeconds(t2)
            )
    sort.asc = boundSort("ASC")
    sort.desc = boundSort("DESC")

    return () => {
      this.setState(
        produce(draft => {
          const { [type]: t } = draft.sort
          console.log({ t, OrderEnum })
          const order = OrderEnum[t].toLowerCase()
          const f = sort[order]

          draft.sort[type] = ~t
          draft.messages.sort((...args) => f(...args))
        }),
        () => console.log(this.state)
      )
    }
  })

  render() {
    const {
      props: { messages },
      state: {
        sort: { date, time },
      },
    } = this

    const orders = {
      date: OrderEnum[date],
      time: OrderEnum[time],
    }

    return (
      <Table>
        <Headers sort={this.onClick} order={orders} />
        <TableBody>
          {messages.map(({ _id, date: d, type, text }) => {
            const parsedDate = new Date(d)
            const [date, time] = [
              parsedDate.toLocaleDateString(),
              parsedDate.toLocaleTimeString(),
            ]

            return (
              <MessageThread
                key={_id}
                date={date}
                time={time}
                msgType={type}
                message={text}
              />
            )
          })}
        </TableBody>
      </Table>
    )
  }
}
